import React, { useEffect, useState } from "react";
import { InvokePublication } from "../Services/ServicesFunctions";
import { useRequest } from "ahooks";
import {
  Container,
  Navbar,
  Nav,
  Form,
  FormControl,
  Button,
} from "react-bootstrap";

const MainNavMenu = (props) => {
  const [activeEl, setActiveEl] = useState(null);
  const { data, error, run, loading } = useRequest(InvokePublication, {
    manual: true,
  });

  useEffect(() => {
    run(0, 0);
    return () => console.log("MainNavMenu unmount");
  }, []);

  const handleBasicClick = (val) => {
    if (val === activeEl) {
      return;
    }
    setActiveEl(val);
  };

  if (data) {
    return (
      <Navbar
        bg="dark"
        expand="sm"
        sticky="top"
        className="justify-content-between"
      >
        <Container fluid>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <ul className={"navbar"}>
              {data.map((menuItem) => (
                <MainNavMenuItem
                  key={menuItem.Id}
                  MenuItem={menuItem}
                  SetSideNavMenuSelectedId={props.setSideNavMenuSelectedId}
                  ActiveEl={activeEl}
                  SetActiveEl={setActiveEl}
                  HandleBasicClick={handleBasicClick}
                />
              ))}
            </ul>
            {/* <Form className="d-flex">
              <FormControl
                type="search"
                placeholder="Поиск"
                className="me-2"
                aria-label="Поиск"
              />
              <Button variant="outline-success">Поиск</Button>
            </Form> */}
          </Navbar.Collapse>
        </Container>
      </Navbar>
    );
  }
};

function MainNavMenuItem(props) {
  if (props.MenuItem.HeadersCount != 0) {
    return (
      <li
        onMouseEnter={() => console.log("hovered!" + props.MenuItem.Id)}
        className={
          props.ActiveEl === props.MenuItem.Id
            ? "active main-nav-link"
            : "inactive main-nav-link"
        }
        onClick={() => {
          props.HandleBasicClick(props.MenuItem.Id);
          props.SetSideNavMenuSelectedId(props.MenuItem.Id);
        }}
      >
        {props.MenuItem.TaskName}
      </li>
    );
  }
}

export default MainNavMenu;
