import React, { useEffect, useState } from "react";
import { InvokePublication } from "../Services/ServicesFunctions";
import { useRequest } from "ahooks";
import { ListGroup, Spinner, Nav } from "react-bootstrap";
import AddContent from "./AddContent";

const SideNavMenu = (props) => {
  const [activeEl, setActiveEl] = useState(null);
  const { data, error, run, loading } = useRequest(InvokePublication, {
    manual: true,
  });

  const handleBasicClick = (val) => {
    if (val === activeEl) {
      return;
    }
    setActiveEl(val);
  };

  useEffect(() => {
    run(props.sideNavMenuSelectedId, 0);
    return () => {
      //props.SetShowContentPage(false);
      console.log("SideNavMenu unmouny");
    };
  }, [props.sideNavMenuSelectedId]);
  if (loading) {
    return (
      <div className="load-wrapper">
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      </div>
    );
  }

  if (error) {
    return <div>Error</div>;
  }

  if (data) {
    return (
      <ul className="flex-column side-nav">
        {data.map((menuItem) => (
          <SideNavMenuItem
            key={menuItem.Id}
            MenuItem={menuItem}
            SetContentNavMenuSelectedId={props.setContentNavMenuSelectedId}
            SetShowContentPage={props.SetShowContentPage}
            ActiveEl={activeEl}
            SetActiveEl={setActiveEl}
            HandleBasicClick={handleBasicClick}
          />
        ))}
        <AddContent />
      </ul>
    );
  }
};

const SideNavMenuItem = (props) => {
  if (props.MenuItem.DocsCount != 0) {
    return (
      <li
        className={
          props.ActiveEl === props.MenuItem.Id
            ? "active side-nav-link"
            : "inactive side-nav-link"
        }
        onClick={() => {
          props.SetShowContentPage(false);
          props.SetContentNavMenuSelectedId(props.MenuItem.Id);
          props.HandleBasicClick(props.MenuItem.Id);
        }}
      >
        {props.MenuItem.TaskName}
      </li>
    );
  }
};

export default SideNavMenu;
